"use client"; // necessary since we're using hooks

import { useEffect, useState } from "react";
import styles from "./quizzes.module.css";

interface Quiz {
  uuid: string;
  title: string;
  createdAt: string;
  _count: { questions: number };
}

export default function Quizzes() {
  const [quizzes, setQuizzes] = useState<Quiz[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchQuizzes() {
      try {
        const res = await fetch("http://localhost:3000/quizzes"); // our backend
        if (!res.ok) throw new Error("Error fetching quizzes");
        const data = await res.json();
        setQuizzes(data);
      } catch (err) {
        console.error(err);
      } finally {
        setLoading(false);
      }
    }

    fetchQuizzes();
  }, []);

  if (loading) return <p>Loading quizzes...</p>;
  if (quizzes.length === 0) return <p>Not found.</p>;

  return (
    <div>
      <h1>Quizzes</h1>
      <ul>
        {quizzes.map((quiz) => (
          <li key={quiz.uuid}>
            <h2>{quiz.title}</h2>
            <p>{quiz._count.questions} questions</p>
          </li>
        ))}
      </ul>
    </div>
  );
}
